@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;300;400;500&display=swap");

* {
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Noto Sans", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  /* background: linear-gradient(120deg, #060613 , #070716, #060610, #030310, #020212); */
  background: linear-gradient(
    120deg,
    var(--dark-blue),
    var(--medium-blue),
    var(--dark-blue)
  );
  scroll-behavior: smooth;
    
}

body::-webkit-scrollbar {
  display: none;
}

p,
a,
h1,
h2,
h3,
h4,
h5 {
  text-transform: capitalize;
}

/* === Variables === */

:root {
  /* === Color === */
  --black: #000;
  --white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
  --medium-blue: #111135;
  --pure-blue: #211d95;
  --dark-blue: rgb(12, 13, 33);
  --light-blue: rgb(28, 70, 242);
  --grey: #181b20;
  --color-bg-varient: #2c2c6c;
  --color-primary-varient: rgba(77, 181, 255, 0.4);
  --light-blue-2: #566ffd;
  --color-primary: #4db5ff;
  --dark-white: #b0b0b0;
  --color-bg: #1f1f38;

  /* === Transitions === */
  --transition: 0.6s all ease;
  --transition-fast: 0.3s all ease;
  --shadow: 0px 0px 20px 0px rgb(132 144 255 / 20%);
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

a {
  text-decoration: none;
  color: var(--color-primary);
  
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

/* === Common Classes === */

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  background: transparent;
  border-radius: 0.4rem;
  
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}
.btn:hover {
  background: transparent;
  color: var(--white);
  /* border: transparent; */
  
}
.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
  font-size: 1rem;
  
}

.grid {
  display: grid;
}

.grid-two-column {
  grid-template-columns: 1fr 1fr;
}

.grid-three-column {
  grid-template-columns: repeat(3, 1fr);
}

.grid-four-column {
  grid-template-columns: repeat(4, 1fr);
}

.flex {
  display: flex;
}

.hor-center {
  justify-content: center;
}

.top-center {
  align-items: center;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

/* ==========================================
=============================================
=============================================
=============  HOME PAGE ====================
=============================================
=============================================
=============================================
*/

/* === HEADER STYLES === */

.header_top {
  height: 13vh;
}

/* === Navbar Styles === */

nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}
nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}
nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}
nav a.active {
  background: var(--color-bg);
  color: white;
}

/* === Hero Section Styles === */

.hero-section {
  padding: 4rem;
  /* margin: 2rem 4rem; */
  width: auto;
  background: linear-gradient(110deg, var(--color-bg), var(--grey));
  /* border-radius: 2rem; */
  height: auto;
  transition: var(--transition);
  min-height: 60vh;
}

.hero-section:hover {
  /* transform: scale(1.006, 1.006); */
}

.hero-part-1 {
  padding: 5rem 3rem 2rem;
  color: var(--dark-white);
  transition: var(--transition);  
}
.hero-part-1:hover {
  color: var(--white);
}
.hero-data {
  margin-bottom: 1rem;
}
.hero-data > h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}
.hero-data > h2 {
  font-size: 2rem;
  /* margin-bottom: 1rem; */
}

.hero-data > p {
  font-size: 1.1rem;
  max-width: 80%;
  margin-bottom: 1rem;
}

.hero-btns {
  overflow: hidden;
  
}

.hero-btns > button {
  margin-right: 1rem;
  
}

.hero-image-div {
  width: 50%;
  padding: 8rem;
  background: url('./assets/javascape-technologies-logo-zip-file/png/logo-no-background.png');
  height: 45vh;
  display: flex;    
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  transition: var(--transition);
}

.hero-image {
  width: 100%;
  transition: var(--transition);
}

.hero-image:hover {
  transform: rotate(20deg);
}
.hero-image-div:hover {
  transform: rotate(-10deg);
}

/* === Bio Data === */

.biodata-section {
  color: var(--color-light);
  transition: var(--transition-fast);
  height: auto;
  padding: 5rem 0rem;
}

.biodata-section:hover {
  color: var(--white);
}

.left-side-biodata {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.left-side-biodata > img {
  width: 60%;
  height: 90%;
  overflow: hidden;
  /* box-shadow: -2rem 2rem 0 0 var(--light-blue); */
  transition: var(--transition);
  border-radius: 2rem;
}
.left-side-biodata > img:hover {
  transform: scale(1.05);
  overflow: hidden;
  box-shadow: -2rem 2rem 0 0 var(--light-blue);
}

.right-side-biodata {
  padding: 4rem 3rem 0;
}

.common-heading {
  font-size: 2.8rem;
  font-weight: 600;
  /* color: var(--color-light); */
  margin-bottom: 1rem;
  text-transform: capitalize;
  position: relative;
}

.bio-data-stats {
  display: flex;
  padding: 6.4rem 0;
  flex-direction: column;
  gap: 3rem;
}

.bio-stats h3 {
  margin-bottom: 1rem;
}

.bio-progress-bar {
  width: 90%;
  overflow: hidden;
  height: 0.8rem;
  background-color: var(--grey);
  border-radius: 10rem;
  /* box-shadow: var(--shadow); */
  position: relative;
}

.bio-progress{
  width: 90%;
  overflow: hidden;
  height: 0.8rem;
  background-color: var(--grey);
  border-radius: 10rem;
  /* box-shadow: var(--shadow); */
  position: relative;
}

.bio-progress-bar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: inherit;
  background: var(--light-blue-2);
  border-radius: 10rem;
}

.bio-progress-2::before {
  width: 99%;
}

.bio-progress-3::before {
  width: 90%;
}

.bio-progress-4::before {
  width: 85%;
}

/* === portfoklio section === */

.portfolio-section {
  padding: 4rem 2rem 6rem;
  background: linear-gradient(110deg, var(--color-bg), var(--dark-blue));
  color: var(--color-light);
  transition: var(--transition);
  gap: 1rem;
  font-size: larger;
}

.portfolio-projects {
  gap: 2rem;
  margin: 2rem 0;
}

.portfolio-section:hover {
  color: var(--white);
}

.portfolio__item {
  background: var(--light-blue-2);
  padding: 1.2rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}
.portfolio__item:hover {
  background: transparent;
  border-color: var(--color-primary-varient);
}
.portfolio__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
}

.portfolio__item-image > img {
  width: 100%;
}

.portfolio__item h3 {
  margin: 1.2rem 0 2rem;
}
.portfolio__item-cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}
.portfolio__btns {
  display: flex;
  justify-content: center;
  gap: 1rem;
  
  margin-bottom: 3rem;
}
.btn__port {
  width: max-content;
  background: transparent;
  display: inline-block;
  font-size: 1rem;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  
  border-radius: 0.4rem;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

/* === section-work-data === */

.section-work-data {
  background: linear-gradient(
    110deg,
    var(--color-primary),
    var(--light-blue-2),
    var(--light-blue)
  );
  height: 20vh;
  color: var(--color-light);
  transition: var(--transition-fast);
}

.section-work-data:hover {
  color: var(--white);
}

.all-counters {
  gap: 2rem;
  text-align: center;
}

.counter-numbers {
  font-size: 2rem;
  font-weight: 700;
}

/* ===========================================
our services Section Start
======================================= */

.section-services {
  padding: 4rem 3rem;
  color: var(--color-light);
  transition: var(--transition-fast);
}

.section-services:hover {
  color: white;
}

.section-services-data {
  font-size: 90%;
  margin: 0;
}

.intheservices {
  font-size: 2.1rem;
}

.section-services .grid {
  margin-top: 4rem;
  gap: 5rem;
  width: auto;
  padding: 0 10rem;
}

.service-box {
  text-align: center;
  /* box-shadow: var(--shadow); */
  /* background: #ec4b813b; */
  background: linear-gradient(
    110deg,
    var(--color-primary),
    var(--light-blue-2)
  );
  border-radius: 5px;
  padding: 6.4rem 3.4rem;
  transition: all 0.2s linear;
  color: black;
  width: auto;
  height: 17rem;
  overflow: hidden;
}

.service-box:hover,
.service-box:active {
  transform: translateY(-1rem);
}

.service-icon {
  width: 8rem;
  height: 8rem;
  background-color: transparent;
  color: black;
  display: inline-block;
  border-radius: 56% 44% 70% 30% / 30% 54% 46% 70%;
  padding: 1.4rem 1.8rem;
  position: relative;
  color: var(--color-bg);
  animation: water-wave 3s linear infinite;
}

.service-icon::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
  border-radius: 56% 44% 70% 30% / 30% 54% 46% 70%;
  background: transparent;
  border: 1px solid var(--black);
  animation: water-wave 3s linear infinite;
}

@keyframes water-wave {
  0% {
    border-radius: 56% 44% 70% 30% / 30% 54% 46% 70%;
  }

  50% {
    border-radius: 3% 97% 15% 85% / 72% 0% 100% 28%;
  }

  100% {
    border-radius: 56% 44% 70% 30% / 30% 54% 46% 70%;
  }
}

.service-box h3 {
  text-transform: capitalize;
  margin-top: 3rem;
  margin-bottom: 1rem;
}

/* === RESUME === */

.section-resume {
  /* background: #ec4b8127; */
  background: linear-gradient(110deg, var(--color-bg), var(--grey));
  margin: 4rem 8rem;
  border-radius: 1rem;
  border: 2px solid var(--color-bg);
  color: var(--color-light);
  transition: var(--transition-fast);
  padding: 4rem;
}

.section-resume:hover {
  color: white;
}

.resume-img {
  width: 100%;
}

.resume-img > img {
  width: 60%;
  /* box-shadow: -2rem 2rem 0 0 var(--light-blue); */
  transition: var(--transition);
  border-radius: 2rem;
}
.resume-img > img:hover {
  transform: scale(1.05);
  box-shadow: 2rem 2rem 0 0 var(--light-blue);
}

/* resume right side style  */
.resume-para {
  font-size: 2rem;
  letter-spacing: 0;
  text-transform: capitalize;
  color: var(--heading);
}

.resume-data-subsection {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4.8rem;
  margin-top: 4.4rem;
  padding-bottom: 2.4rem;
  border-bottom: 1px solid var(--para-tint);
}

.resume-data-left .grid {
  gap: 1.5rem;
}

.resume-data-left h3 {
  margin-bottom: 1rem;
}

.resume-data-left .grid p,
.resume-data-right p {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.resume-data-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.resume-data-right .resume-data-button {
  font-size: 0.95rem;
  text-transform: uppercase;
  color: var(--helper);
  padding: 0.3rem 1rem;
  min-width: 50%;
  background: var(--color-bg);
  justify-content: center;
  border-radius: 10rem;
  margin-bottom: 0.5rem;
}

.resume-data-bottom-subsection {
  margin-top: 4.8rem;
}

.resume-data-bottom-subsection .margin-small {
  margin-top: 1.8rem;
}

/* === REVIEWS SECTION === */

.testimonial-section {
  padding: 4rem 0;
  width: 100%;
  text-align: center;
  color: var(--color-light);
  transition: var(--transition-fast);
}

.testimonial-section:hover{
  color: var(--white);
}

.alltestimonials {
  font-size: 120%;
  width: 100%;
}

.testimonial {
  height: 20vh;
  background: var(--light-blue);
  border-radius: 2rem;
  width: auto;
  width: 50%;
  margin: 2rem 16rem;
  padding: 2rem 2rem 1rem;
  transition: var(--transition);
  border: 1px solid var(--color-bg-varient);
}

.testimonial:hover {
  background: none;
}

.testimonial > div {
  width: 100%;
} 
.testimonial > div > h5 {
  float: right;
}

.dots{
  /* gap: 2rem; */
}

.deactive {
  color: white;
}

.active {
  color: var(--color-bg-varient);
}

/* === CONTACT SECTION === */

.contact-section {
  gap: 2rem;
  color: var(--color-light);
  padding: 4rem 0;
  transition: var(--transition-fast);
}

.contact-section:hover {
  color: var(--white);
}

.contact__container {
  width: 60%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}

.contact__options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.contact__option {
  background: var(--color-bg-varient);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}
.contact__option:hover {
  background: transparent;
  border-color: var(--color-primary-varient);
}
.contact__option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.contact__option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}
form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
input,
textarea {
  width: auto;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-varient);
  resize: none;
  
  color: white;
  outline: none;
}

/* === footer section === */

footer {
  background: var(--color-primary);
  padding: 3rem 0;
  font-size: 0.9rem;
  text-align: center;
  margin-top: 7rem;
}
footer a {
  color: var(--color-bg);
}
.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}
.footer__socials a {
  background: var(--color-bg);
  color: white;
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}
.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}
.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}

