/* ==========================================
=============================================
=============================================
=============  LOGIN PAGE ====================
=============================================
=============================================
=============================================
*/

.login-section {
  height: auto;
  border-radius: 2rem;
  font-size: 70%;
  padding: 1rem;
  width: auto;
  margin: 0;
}

.login-board {
