.filed_in_back_panel {
  display: flex;
  flex-direction: column;
}

.portfolio_in_the_back_panel {
  display: flex;
  flex-direction: column;
}

.resume_in_the_back_panel button{
  padding: 0.4rem 1rem;
}

.resume_data_info_in_back_panel {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  margin-bottom: 4rem;
}

.top_header_in_admin {
  font-size: 90%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.top_header_in_admin button {
  display: flex;
  gap: 1rem;
}


  
  /* ====================================
  TEAM  SECTION
  ======================================= */

  .out_team_members {
    grid-template-columns: repeat(1, 1fr);
  }
}


/* ==========================================
=============================================
=============================================
=============  LOGIN PAGE ====================
=============================================
=============================================
=============================================
*/

.login-section {
  height: auto;
  margin: 4rem;
}

.login-board {
  padding: 4rem;
  color: var(--color-light);
  border-radius: 2rem;
  gap: 3rem;
  transition: var(--transition-fast);
  background: linear-gradient(110deg, var(--color-bg), var(--medium-blue));
}
.login-board:hover {
  color: var(--white);
}

.login-btns {
  gap: 2rem;
  
}

.login_input{
  border: none;
  border-bottom: 2px solid var(--color-bg-varient);
  border-radius: 0%;
  padding: 0 0 0.5rem 0;
}

.input-login{
  margin-top: 2rem;
}

.gapper {
  gap: 4rem;
  width: auto;
}


/*  === TEAM MEMBERS === */

.out_team_members {
  gap: 2rem;
}

.team__member{
  background: var(--color-bg2);
  padding: 2rem;
  border:  1px solid transparent;
  transition: var(--transition-fast);
  position: relative;
}
.team__member:hover{
  background: transparent;
  border-color: var(--color-primary);
}
.team__member-image img{
  filter: saturate(0);
  /* height: auto; */
  max-height: 20rem;
  transition: var(--transition-fast);
}
.team__member:hover img{
  filter: saturate(1);
  height: auto;
}
.team__member-info *{
  text-align: center;
  margin-top: 1.4rem;
}
.team__member-info p {
  color: var(--color-light );
}
.team__member-socials{
  display: none;
  position: absolute;
  top: 50%;
  transition: var(--transition);
  right: 0;
  transform: translateY(-50%);
  /* display: flex; */
  flex-direction: column;
  background: var(--color-primary);
  border-radius: 1rem 0 0 1rem;
  box-shadow: -2rem 0 2rem rgba(0, 0, 0, 0.3);
} 
.team__member:hover .team__member-socials{
  display: flex;
}
.team__member-socials a{
  padding: 1rem;
}